import { Component, Vue } from 'vue-property-decorator';


@Component
export default class CrucibleEncounters extends Vue {
    inputDay: number = 0;
    inputTile: number = 0;

    environment: string = '';
    difficulty: string = '';
    encounterType: string = '';
    distance: string = '';

    creatures: string = '';
    disposition: string = '';
    awareness: string = '';

    eventType: string = '';
    trapType: string = '';

    GetRandomInt(max: number, modifier: number = 0): number {
        return Math.floor(Math.random() * Math.floor(max)) + 1 + modifier;
    }

    GetEnvironmentResult(): string {
        let result: string = '';
        const roll: number = this.GetRandomInt(10);
        if(roll <= 5){
            result = 'Clear';
        } else if (roll >= 6 && roll <= 8){
            result = 'Mild';
        } else if (roll === 9) {
            result = 'Obscuring'
        } else if (roll === 10) {
            result = 'Dangerous'
        }
        return result;
    }

    GetDifficultyResult(): string {
        let result: string = '';
        const roll: number = this.GetRandomInt(20) + Number(this.inputDay)+ Number(this.inputTile);
        if(roll <= 10){
            result = 'None';
        } else if (roll >= 10 && roll <= 19){
            result = 'Easy';
        } else if (roll >= 20 && roll <= 27){
            result = 'Medium';
        } else if (roll >= 28 && roll <= 34){
            result = 'Hard';
        } else if (roll >= 35){
            result = 'Deadly';
        }
        return result;
    }

    GetEncounterTypeResult(): string {
        let result: string = '';
        const roll: number = this.GetRandomInt(8);

        if(roll <= 2){
            result = 'Event';
        } else if (roll >= 3 && roll <= 6){
            result = 'Creatures';
        } else if (roll >= 7){
            result = 'Trap';
        }
        return result;
    }

    GetDistanceResult(): string {
        let result: string = '';
        const roll: number = this.GetRandomInt(4);

        if(roll === 1){
            result = 'Distant / ~1 Mile';
        } else if (roll === 2){
            result = 'Far / 100 ~ 200 ft.';
        }  else if (roll === 3){
            result = 'Near / 30 ~ 60 ft.';
        } else if (roll === 4){
            result = 'Imminent / 1 ~ 10 ft.';
        }
        return result;
    }

    GetDispositionResult(): string {
        let result: string = '';
        const roll: number = this.GetRandomInt(6);

        if(roll === 1){
            result = 'Friendly / Helpful';
        } else if (roll === 2){
            result = 'Afraid / Needs Help';
        }  else if (roll === 3){
            result = 'Neutral / Passive';
        } else if (roll === 4){
            result = 'Initially On Guard';
        } else if (roll === 5){
            result = 'Immediately Aggressive';
        } else if (roll === 6){
            result = 'Actively After the Party';
        }
        return result;
    }

    GetAwarenessResult(): string {
        let result: string = '';
        const roll: number = this.GetRandomInt(12);

        if(roll <= 2){
            result = 'Creatures Surprised';
        } else if (roll >= 3 && roll <= 6){
            result = 'Neither Surprised';
        } else if (roll >= 7 && roll <= 10){
            result = 'Both Surprised';
        } else if (roll >= 11){
            result = 'Party Surprised';
        }
        return result;
    }

    GetCreatureTypeResult(difficulty: string): string {
        let result: string = '';
        const roll: number = this.GetRandomInt(6);

        if(difficulty === 'None'){
            result = 'Nothing'
        } else if (difficulty === 'Easy'){
            if(roll === 1){
                result = this.GetRandomInt(2) + ' Wild Creatures';
            } else if (roll === 2){
                result = this.GetRandomInt(4, 1) + ' Wild Creatures ';
            }  else if (roll === 3){
                result = this.GetRandomInt(4) + ' Melee Minions';
            } else if (roll === 4){
                result = this.GetRandomInt(2) + ' Melee Minions'
                result += ' + ' + this.GetRandomInt(2) + ' Ranged Minions';
            } else if (roll === 5){
                result = this.GetRandomInt(2) + ' Melee Soldiers'
            } else if (roll === 6){
                result = this.GetRandomInt(2) + ' Ranged Soldiers'
                result += ' + ' + this.GetRandomInt(4, -1) + ' Melee Minions';
            }
        } else if (difficulty === 'Medium'){
            if(roll === 1){
                result = this.GetRandomInt(2) + ' Melee Soldiers'
                result += ' + ' + this.GetRandomInt(4) + ' Ranged Minions';
            } else if (roll === 2){
                result = this.GetRandomInt(2) + ' Ranged Soldiers'
                result += ' + ' + this.GetRandomInt(4) + ' Melee Minions';
            }  else if (roll === 3){
                result = this.GetRandomInt(4, 1) + ' Melee Minions'
                result += ' + 1 Specialist';
            } else if (roll === 4){
                result = this.GetRandomInt(4, 1) + ' Ranged Minions'
                result += ' + 1 Melee Brute';
            } else if (roll === 5){
                result = this.GetRandomInt(4, 1) + ' Melee Soldiers'
            } else if (roll === 6){
                result = this.GetRandomInt(4) + ' Melee Minions'
                result += ' + ' + this.GetRandomInt(4) + ' Ranged Minions'
                result += ' + 1 Specialist';
            }
        } else if (difficulty === 'Hard'){
            if(roll === 1){
                result = this.GetRandomInt(4, 2) + ' Melee Minions'
                result += ' + ' + this.GetRandomInt(4, 2) + ' Ranged Minions'
                result += ' + 1 Specialist';
            } else if (roll === 2){
                result = this.GetRandomInt(4) + ' Melee Brute'
                result += ' + ' + this.GetRandomInt(4, 1) + ' Ranged Minions'
            }  else if (roll === 3){
                result = this.GetRandomInt(4, 2) + ' Melee Soldier'
                result += ' + ' + this.GetRandomInt(4, 2) + ' Ranged Minions'
            } else if (roll === 4){
                result = this.GetRandomInt(4, 1) + ' Melee Brute'
                result += ' + 1 Specialist';
            } else if (roll === 5){
                result = this.GetRandomInt(6, 1) + ' Melee Minion'
                result += ' + ' + this.GetRandomInt(4, 1) + ' Ranged Soldier'
            } else if (roll === 6){
                result = (this.GetRandomInt(4) + this.GetRandomInt(4,2)) + ' Melee Minion'
                result += ' + ' + this.GetRandomInt(2) + ' Specialist'
            }
        } else if (difficulty === 'Deadly'){
            if(roll === 1){
                result = this.GetRandomInt(4) + ' Melee Brute'
                result += ' + ' + this.GetRandomInt(6) + ' Ranged Minions'
                result += ' + 1 Master';
            } else if (roll === 2){
                result = this.GetRandomInt(4, 1) + ' Melee Soldier'
                result += ' + ' + this.GetRandomInt(4, 1) + ' Ranged Soldier'
                result += ' + ' + this.GetRandomInt(2) + ' Specialist'
            }  else if (roll === 3){
                result = this.GetRandomInt(4, 1) + ' Melee Soldier'
                result += ' + ' + this.GetRandomInt(4, 1) + ' Ranged Soldier'
                result += ' + ' + this.GetRandomInt(2) + ' Melee Brute'
            } else if (roll === 4){
                result = this.GetRandomInt(6, 1) + ' Melee Brute'
                result += ' + 1 Specialist'
                result += ' + 1 Master'
            } else if (roll === 5){
                result = (this.GetRandomInt(4) + this.GetRandomInt(4,2)) + ' Melee Minion'
                result += ' + ' + this.GetRandomInt(4) + ' Ranged Brute'
                result += ' + 1 Specialist'
                result += ' + 1 Master'
            } else if (roll === 6){
                result = 'Wandering Legend';
            }
        }
        return result;
    }

    GetEventTypeResult(): string {
        let result: string = '';
        const roll: number = this.GetRandomInt(6);

        if(roll === 1){
            result = 'Skill Encounter';
        } else if (roll === 2){
            result = 'Social Encounter';
        }  else if (roll === 3){
            result = 'Empty Camp / Abandoned Cart';
        } else if (roll === 4){
            result = 'Natural Occurence';
        } else if (roll === 5){
            result = 'Social Creature';
        } else if (roll === 6){
            result = 'Glitch in the System';
        }
        return result;
    }

    GetTrapTypeResult(): string {
        let result: string = '';
        const roll: number = this.GetRandomInt(12);

        if(roll <= 3){
            result = 'Natural Danger';
        } else if (roll >= 4 && roll <= 5){
            result = 'Constructed Trap';
        } else if (roll >= 6){
            result = 'Test of the Gods';
        }
        return result;
    }

    GenerateEncounter(){
        this.environment = this.GetEnvironmentResult();
        this.difficulty = this.GetDifficultyResult();
        if(this.difficulty === "None"){
            this.creatures = '';
            this.eventType = '';
            this.disposition = '';
            this.awareness = '';
            this.trapType = '';
            this.encounterType = '';
            this.distance = '';
        } else {
            this.encounterType = this.GetEncounterTypeResult();
            this.distance = this.GetDistanceResult();

            if(this.encounterType === 'Trap'){
                this.creatures = '';
                this.eventType = '';
                this.disposition = '';
                this.awareness = '';
                this.trapType = this.GetTrapTypeResult();
            } else if (this.encounterType === 'Event'){
                this.creatures = '';
                this.disposition = '';
                this.awareness = '';
                this.trapType = '';
                this.eventType = this.GetEventTypeResult();
            } else if (this.encounterType === 'Creatures'){
                this.trapType = '';
                this.eventType = '';
                this.creatures = this.GetCreatureTypeResult(this.difficulty);
                this.disposition = this.GetDispositionResult();
                this.awareness = this.GetAwarenessResult();
            }
        }
    }
}
